'use client'

import { useRef } from 'react'
import { motion, useMotionValue, useTransform } from 'framer-motion'

export default function Ultra3DButton({ children, href = '#', className = '' }) {
  const el = useRef(null)

  // We'll use framer-motion values for smooth tilt
  const mouseX = useMotionValue(0)
  const mouseY = useMotionValue(0)

  // map mouse values to rotation angles
  const rotateY = useTransform(mouseX, [-100, 100], [12, -12])
  const rotateX = useTransform(mouseY, [-100, 100], [-12, 12])

  function handleMove(e) {
    const rect = el.current.getBoundingClientRect()
    const px = (e.clientX - rect.left) - rect.width / 2
    const py = (e.clientY - rect.top) - rect.height / 2
    // scale the pointer offsets
    mouseX.set(px / (rect.width / 2) * 100)
    mouseY.set(py / (rect.height / 2) * 100)
  }

  function handleLeave() {
    mouseX.set(0)
    mouseY.set(0)
  }

  return (
    <motion.a
      ref={el}
      href={href}
      onMouseMove={handleMove}
      onMouseLeave={handleLeave}
      style={{ rotateX, rotateY, transformPerspective: 800 }}
      whileTap={{ scale: 0.97 }}
      className={`relative inline-block group ${className}`}
      // keep button accessible
      role="button"
      aria-label={typeof children === 'string' ? children : 'call to action'}
    >
      {/* Depth shadow layer */}
      <span className="absolute -inset-1 rounded-xl blur-[18px] opacity-30 bg-gradient-to-r from-pink-500 to-indigo-500 transition-all duration-500 group-hover:opacity-80 pointer-events-none z-0"></span>

      {/* 3D surface */}
      <span className="relative z-10 block rounded-xl px-6 py-3 text-white font-semibold bg-gradient-to-r from-indigo-600 to-sky-500 shadow-2xl transform-gpu">
        {/* sheen overlay */}
        <span className="absolute inset-0 rounded-xl pointer-events-none overflow-hidden">
          <span className="sheen"></span>
        </span>

        {/* text */}
        <span className="relative z-20">{children}</span>
      </span>

      {/* subtle inner ring for glow (behind surface but above depth) */}
      <span className="absolute -inset-2 rounded-xl border border-white/10 z-5 pointer-events-none"></span>
    </motion.a>
  )
}
